

.productList{
    flex: 4;
}
.productListItem {
    display: flex;
    flex-direction: row;
    align-items: end;
    justify-content: end;
    justify-self: end;
    justify-items: end;
    align-self: flex-end;
    align-content: end;
}

.productListImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.productListEdit {
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color: #3bb077;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}

.productListDelete {
    color: red;
    cursor: pointer;
}